* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  list-style-type: none;
  margin: 0;

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 35px; }

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    height: 13px;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  .subheader_call {
    margin-top: 19px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    text-align: right; }

  .subheader_phone {
    display: block;
    text-align: right;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 12px; }

  .subheader_btn {
    margin: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 4px; } }

.promo {
  min-height: 799px;
  padding: 150px 0 135px 0;
  background: url("../img/foto/%D1%84%D0%BE%D1%82%D0%BE%201.jpg") center (center / cover) no-repeat;

  .promo_header {
    margin: 0;
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    text-align: center;
    line-height: 60px; }

  .promo_subheader {
    margin: 0;
    margin-top: 22px;
    font-size: 60px;
    color: #ffffff;
    font-weight: 400;
    font-size: 36px;
    text-transform: uppercase;
    text-align: center;
    line-height: 36px; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 120px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    border-radius: 4px;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  margin: 0 auto;
  padding: 5px 12px;
  width: 112px;
  height: 24px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 40px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  line-height: 48px;
  text-transform: uppercase;
  text-align: center; }

.subtitle {
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center; }
.reasons {
  .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%;
    margin-right: 26px; }

  .reasons_descr {
    width: 343px; }

  .reasons_subtitle {
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400;
    line-height: 21px; }

  .reasons_text {
    font-size: 15px;
    font-weight: 400;
    line-height: 21px;
    color: #202020; }
  .mobile {
    min-height: 864px;
    padding: 48px 0 80px 0;
    background-image: url("../img/fon.png"); } }








































